@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --veryDarkGray: hsl(0, 0%, 17%);
  --darkGray: hsl(0, 0%, 59%);
  --borderRadius: 16px;
  font-size: 18px;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 1rem;
}
h1 {
  font-size: 1.8rem;
  font-weight: 400;
  color: #ffffff;
}
input,
button {
  border: none;
}
input:focus,
button:focus {
  outline: none;
}
@media screen and (max-width: 768px) {
  h1 {
    font-size: 1.4rem;
  }
}

.main {
  position: relative;
}

.ip-search {
  display: flex;
  justify-content: center;
}
.ip-search__field {
  height: 3.2rem;
  border: none;
  outline: none;
  cursor: pointer;
  width: 600px;
  border-radius: 15px;
  padding-left: 1.5rem;
  font-size: 1rem;
}
.ip-search__field--hidden {
  display: none;
}
.button-submit {
  width: 3.4rem;
  background-image: url(/static/media/icon-arrow.53ef8ed8.svg);
  background-color: var(--veryDarkGray);
  background-repeat: no-repeat;
  background-position: center;
  height: 100%;
  border-radius: 0 var(--borderRadius) var(--borderRadius) 0;
  margin-left: -1.5rem;
  cursor: pointer;
}
.button-submit:hover {
  background-color: #505050;
}
@media screen and (max-width: 765px) {
  .ip-search {
    margin-left: auto;
    margin-right: auto;
    width: 86%;
  }
  .ip-search__field::-webkit-input-placeholder {
    font-size: 0.7rem;
  }
  .ip-search__field:-ms-input-placeholder {
    font-size: 0.7rem;
  }
  .ip-search__field::-ms-input-placeholder {
    font-size: 0.7rem;
  }
  .ip-search__field::placeholder {
    font-size: 0.7rem;
  }
}

.header {
  height: 40vh;
  background-image: url(/static/media/pattern-bg.8d3f4ad6.png);
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  padding-top: 1.5rem;
}
.header__title {
  margin-bottom: 1.6rem;
}
@media screen and (max-width: 765px) {
  .header {
    background-position: center;
  }
}

.tab {
  position: relative;
  width: 25%;
  line-height: 35px;
  padding-left: 1.3rem;
}
.tab__title {
  color: var(--darkGray);
  font-weight: 700;
  letter-spacing: 1px;
}
.tab__content {
  color: var(--veryDarkGray);
  font-weight: 500;
  font-size: 1.5rem;
}
@media screen and (max-width: 768px) {
  .tab {
    padding: 0;
    width: 100%;
    text-align: center;
    line-height: 25px;
  }
  .tab__content {
    font-size: 1rem;
  }
}

.horizontal-card {
  position: absolute;
  top: calc(160px / 2 * -1);
  left: 50%;
  transform: translate(-50%);
  width: 80%;
  background-color: #ffffff;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 1.7rem;
  border-radius: var(--borderRadius);
  z-index: 1000;
}
.horizontal-card--hidden {
  display: none;
}
@media screen and (max-width: 768px) {
  .horizontal-card {
    width: 86%;
    padding: 1rem;
    flex-direction: column;
    align-items: center;
    top: calc(300px / 2.3 * -1);
  }
}
@media screen and (max-height: 750px) {
  .horizontal-card {
    top: -80px;
  }
}

.border-element::before {
  opacity: 0.5;
  position: absolute;
  left: 0;
  top: 50%;
  content: "";
  width: 1px;
  height: 70%;
  transform: translateY(-50%);
  background-color: var(--darkGray);
}
@media screen and (max-width: 768px) {
  .border-element::before {
    display: none;
  }
}

.map-wrapper {
  height: calc(100vh - 40vh);
  position: relative;
}
.leaflet-control-zoom {
  display: none;
}

