.header {
  height: 40vh;
  background-image: url("../../img/pattern-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  padding-top: 1.5rem;
}
.header__title {
  margin-bottom: 1.6rem;
}
@media screen and (max-width: 765px) {
  .header {
    background-position: center;
  }
}
